import React from 'react'
import styled from 'styled-components'
import avatar from '../../img/avatar.png'
import { signout, login , register} from '../../utils/Icons'
import { menuItems } from '../../utils/menuItems'
import { useGlobalContext } from '../../context/globalContext';

function Navigation() {

    const {logged, setLogged, active, setActive} = useGlobalContext();

    return (
        <NavStyled>
            <div className='direction'>
            <div className="user-con">
                <img src={avatar} alt="" />
                <div className="text">
                    <h2>{logged ? localStorage.getItem("username") : "Login"}</h2>
                    <p>{logged ? localStorage.getItem("email") : "Please login in"}</p>
                </div>
            </div>
            <ul className="menu-items">
                {menuItems.filter(element => element.id <6).map((item) => {
                    return <li
                        key={item.id}
                        onClick={() => setActive(item.id)}
                        className={active === item.id ? 'active': ''}
                    >
                        {item.icon}
                        <span>{item.title}</span>
                    </li>
                })}
            </ul>
            <ul>
            <div className=" menu-items">
                {logged ?
                    <>
                        <li onClick={() => {localStorage.removeItem("logged"); setLogged(false); setActive(7)}}>
                            {signout} Sign Out
                        </li>
                    </> :
                    <>
                        <li onClick={() => setActive(6)} className={active === 6 ? 'active': ''}>
                            {register} Register
                        </li>
                        <li onClick={() => setActive(7)} className={active === 7 ? 'active': ''}>
                            {login} Login
                        </li>
                    </>
                }
            </div>
            </ul>
            </div>
        </NavStyled>
    )
}

const NavStyled = styled.nav`
    padding: 2rem 1.5rem;
    width: 20rem;
    height: auto;
    background: rgba(252, 246, 249, 0.78);
    border: 3px solid #FFFFFF;
    backdrop-filter: blur(4.5px);
    border-radius: 32px;

    @media screen and (max-width: 500px) {
    width: 100%;
    height: auto;
    overflow: auto;
    padding: 1rem;
  }

    .direction{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 2rem;
    }
    .user-con{
        height: 100px;
        display: flex;
        align-items: center;
        gap: 1rem;
        img{
            width: 80px;
            height: 80px;
            border-radius: 50%;
            object-fit: cover;
            background: #fcf6f9;
            border: 2px solid #FFFFFF;
            padding: .2rem;
            box-shadow: 0px 1px 17px rgba(0, 0, 0, 0.06);
        }
        h2{
            color: rgba(34, 34, 96, 1);
        }
        p{
            color: rgba(34, 34, 96, .6);
        }
    }

    .menu-items{
        flex: 1;
        display: flex;
        flex-direction: column;
        li{
            display: grid;
            grid-template-columns: 40px auto;
            align-items: center;
            margin: .6rem 0;
            font-weight: 500;
            cursor: pointer;
            transition: all .4s ease-in-out;
            color: rgba(34, 34, 96, .6);
            padding-left: 1rem;
            position: relative;
            i{
                color: rgba(34, 34, 96, 0.6);
                font-size: 1.4rem;
                transition: all .4s ease-in-out;
            }
        }
    }

    .active{
        color: rgba(34, 34, 96, 1) !important;
        i{
            color: rgba(34, 34, 96, 1) !important;
        }
        &::before{
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 4px;
            height: 100%;
            background: #222260;
            border-radius: 0 10px 10px 0;
        }
    }
`;

export default Navigation

